<template>
  <div style="background-color: #fff;padding:10px 20px">
    <div :spinning="loading">
      <a-form :form="form" style="margin-top: 10px">
        <a-row :gutter="16" style="border-bottom:1px solid #ddd;margin-top:20px">
          <span style="display:inline-block;padding:10px 10px;font-size:15px">{{ $t('device.title1') }} :</span>
        </a-row>
        <a-row :gutter="16">
          <a-col :xl="{span: 6,offset: 1}" :lg="{span:6,offset: 1}" :md="12" :sm="24">
            <a-form-item :label="$t('meter.index1')">
              <a-input 
                v-decorator="[
                  'description', 
                  {initialValue: data.description, rules: [{required: true,message:$t('meter.index1')+' '+$t('common.notnull')}] }
                ]"
              />
            </a-form-item>
          </a-col>
          <a-col :xl="{span: 6,offset: 1}" :lg="{span:6,offset: 1}" :md="12" :sm="24">
            <a-form-item :label="this.$t('meter.index2')">
              <a-select
                v-decorator="[
                  'meterSort',
                  { initialValue: data.meterSort?[data.meterSort]:['0'],rules: [{required: true,message:$t('meter.index2')+' '+$t('common.notnull')}] }
                ]"
                @change="onChange"
              >
                <a-select-option value="0">
                  {{ $t('dct.METER_SORT0') }}
                </a-select-option>
                <a-select-option value="4">
                  {{ $t('dct.METER_SORT4') }}
                </a-select-option>
                <a-select-option value="7">
                  {{ $t('dct.METER_SORT7') }}
                </a-select-option>
              </a-select>
            </a-form-item>
          </a-col>
          <a-col :xl="{span: 6,offset: 1}" :lg="{span:6,offset: 1}" :md="12" :sm="24">
            <a-form-item :label="this.$t('line.info1')">
              <a-input-group compact>
                <a-input 
                  style="width:80%" 
                  :disabled="true" 
                  v-decorator="[
                    'subName',
                    {initialValue: data.substationDescription, rules: [{required: true,message:$t('line.info1')+' '+$t('common.notnull')}] }
                  ]" />
                <span style="width:20%">
                  <a-button @click="queryProd" icon="search" style="width:100%" :disabled="params.opType!='1'"></a-button>
                </span>
              </a-input-group>
            </a-form-item>
          </a-col>
        </a-row>
        <a-row :gutter="16">
          <a-col :xl="{span: 6,offset: 1}" :lg="{span:6,offset: 1}" :md="12" :sm="24">
            <a-form-item :label="this.$t('device.index5')">
              <dict 
                type="MEAS_MODE"
                table="params"
                v-decorator="[
                  'measMode',
                  { initialValue: data.measMode?[data.measMode]:['0'] , rules: [{required: true,message:$t('device.index5')+' '+$t('common.notnull')}]}
                ]"
              ></dict>
            </a-form-item>
          </a-col>
          <a-col :xl="{span: 6,offset: 1}" :lg="{span:6,offset: 1}" :md="12" :sm="24">
            <a-form-item :label="this.$t('meter.index4')">
              <a-select
                v-decorator="[
                  'important',
                  { initialValue: data.important?[data.important]:['N'],rules: [{required: true,message:$t('meter.index4')+' '+$t('common.notnull')}] }
                ]"
              >
                <a-select-option value="Y">
                  {{ $t('common.yes') }}
                </a-select-option>
                <a-select-option value="N">
                  {{ $t('common.no') }}
                </a-select-option>
              </a-select>
            </a-form-item>
          </a-col>
          <a-col :xl="{span: 6,offset: 1}" :lg="{span:6,offset: 1}" :md="12" :sm="24">
            <a-form-item :label="this.$t('common.status')">
              <dict 
                type="DEVICE_STATUS"
                table="params"
                v-decorator="[
                  'status',
                  { initialValue: data.status?[data.status]:['0'] , rules: [{required: true,message:$t('common.status')+' '+$t('common.notnull')}]}
                ]"
              ></dict>
            </a-form-item>
          </a-col>
        </a-row>
        <a-row :gutter="16">
          <a-col :xl="{span: 6,offset: 1}" :lg="{span:6,offset: 1}" :md="12" :sm="24">
            <a-form-item :label="this.$t('common.vol_level')">
              <dict 
                type="VOL_LEVEL"
                table="params"
                v-decorator="[
                  'volLevel',
                  { initialValue: data.volLevel?[data.volLevel]:['0'] , rules: [{required: true,message:$t('common.vol_level')+' '+$t('common.notnull')}]}
                ]"
              ></dict>
            </a-form-item>
          </a-col>
          <a-col :xl="{span: 6,offset: 1}" :lg="{span:6,offset: 1}" :md="12" :sm="24" v-show="type!='4'">
            <a-form-item :label="$t('mainnetwork.address')">
              <a-input-group compact>
                <a-input 
                  style="width:80%" 
                  :disabled="true" 
                  v-decorator="[
                    'lineDescription',
                    {initialValue: data.lineDescription}
                  ]" />
                <span style="width:20%">
                  <a-button @click="queryLine" icon="search" style="width:100%"></a-button>
                </span>
              </a-input-group>
            </a-form-item>
          </a-col>
          <a-col :xl="{span: 6,offset: 1}" :lg="{span:6,offset: 1}" :md="12" :sm="24" v-show="type=='4'">
            <a-form-item :label="$t('mainnetwork.address')">
              <a-input-group compact>
                <a-input 
                  style="width:80%" 
                  :disabled="true" 
                  v-decorator="[
                    'substationMtName',
                    {initialValue: data.substationMtName }
                  ]" />
                <span style="width:20%">
                  <a-button @click="queryMain" icon="search" style="width:100%" ></a-button>
                </span>
              </a-input-group>
            </a-form-item>
          </a-col>
        </a-row>
        <a-row :gutter="16" style="border-bottom:1px solid #ddd;margin-top:50px">
          <span style="display:inline-block;padding:10px 10px;font-size:15px">{{ $t('device.title2') }} :</span>
        </a-row>
        <a-row :gutter="16">
          <a-col :xl="{span: 6,offset: 1}" :lg="{span:6,offset: 1}" :md="12" :sm="24">
            <a-form-item :label="this.$t('device.index1')">
              <a-input-group compact>
                <a-input 
                  style="width:80%" 
                  :disabled="true" 
                  v-decorator="[
                    'deviceNum',
                    {initialValue: data.deviceNum, rules: [{required: true,message:$t('device.index1')+' '+$t('common.notnull')}] }
                  ]" />
                <span style="width:20%">
                  <a-button @click="queryDev" icon="search" style="width:100%" :disabled="params.opType!='1'"></a-button>
                </span>
              </a-input-group>
            </a-form-item>
          </a-col>
          <a-col :xl="{span: 6,offset: 1}" :lg="{span:6,offset: 1}" :md="12" :sm="24">
            <a-form-item :label="$t('device.index3')">
              <a-input 
                :disabled="true"
                v-decorator="[
                  'address', 
                  {initialValue: data.address}
                ]"
              />
            </a-form-item>
          </a-col>
          <a-col :xl="{span: 6,offset: 1}" :lg="{span:6,offset: 1}" :md="12" :sm="24">
            <a-form-item :label="$t('meter.index5')">
              <pick-date
                showTime
                :defaultValue="data?data.regDate:''"
                v-decorator="[
                  'regDate'
                ]"
              ></pick-date>
            </a-form-item>
          </a-col>
        </a-row>
      </a-form>
    </div> 
    <search-substation ref="selectSub" @ok="loadSubData"></search-substation>
    <search-line ref="selectLine" @ok="loadLineData"></search-line>
    <search-main ref="selectMain" @ok="loadMainData"></search-main>
    <search-device ref="selectDev" @ok="loadDeviceData" :params="serachParam"></search-device>
    <footer-tool-bar :style="{ width: '100%'}">
      <a-button @click="onCancel">{{ $t('common.btn_cancel') }}</a-button>&nbsp;&nbsp;
      <a-button @click="onSubmit" type="primary" v-if="params.opType!='3'">{{ $t('common.btn_ok') }}</a-button>
    </footer-tool-bar>
  </div>
</template>
<script>
  import { axios } from '@/utils/request'
  import Dict from '@/views/public/Dict'
  import DeptSelect from '@/views/public/DeptSelect'
  import SearchSubstation from '@/views/public/SearchSubstation'
  import SearchLine from '@/views/public/SearchLine'
  import SearchMain from '@/views/public/SearchMain'
  import SearchDevice from '@/views/public/SearchDevice'
  import FooterToolBar from '@/components/FooterToolbar'
  import notification from 'ant-design-vue/es/notification'

  export default {
    components: {
      Dict,
      FooterToolBar,
      notification,
      DeptSelect,
      SearchSubstation,
      SearchDevice,
      SearchLine,
      SearchMain
    },
    data() {
      return {
        loading: false,
        type:'0',
        form: this.$form.createForm(this),
        data: {status:0,volLevel:0},
        serachParam:{stationId:'',sort:'0'},
        confirmLoading: false,
        formItemLayout: {labelCol: {lg: {span: 7}, sm: {span: 7}},wrapperCol: {lg: {span: 14}, sm: {span: 14}}}
      }
    },
    props: {
      params: {
        type: Object,
        default: null
      }
    },
    mounted(){
      this.initModel()
    },
    methods: {
      queryProd() {
        this.$refs.selectSub.showModal()
      },
      loadSubData(item) {
        this.data.substationId = item[0]?item[0].id:''
        this.data.deptId = item[0]?item[0].deptId:''
        this.data.lineId=''
        this.data.id=''
        this.serachParam.deptId=item[0]?item[0].deptId:''
        this.serachParam.stationId=item[0]?item[0].id:''
        this.data.substationMtId=''
        this.form.setFieldsValue({'lineDescription': ''})
        this.form.setFieldsValue({'substationMtName': ''})
        this.form.setFieldsValue({'subName': item[0].description})
      },
      queryLine() {
        this.$refs.selectLine.showModal(this.serachParam)
      },
      loadLineData(item) {
        this.data.lineId = item[0]?item[0].id:''
        this.form.setFieldsValue({'lineDescription': item[0].description})
      },
      queryMain() {
        this.$refs.selectMain.showModal(this.serachParam)
      },
      loadMainData(item) {
        this.data.substationMtId = item[0]?item[0].id:''
        this.form.setFieldsValue({'substationMtName': item[0].name})
      },
      queryDev() {
        this.$refs.selectDev.showModal()
      },
      loadDeviceData(item) {
        this.data.id=item[0].devId
        this.form.setFieldsValue({'deviceNum': item[0].devNo})
        this.form.setFieldsValue({'address': item[0].address})
      },
      initModel(){
        if(this.params.opType=='2' || this.params.opType=='3'){
          this.initData()
        }else{
          this.data={status:0,vol_level:0,measMode:0}
        }
      },
      initData(){
        const that = this
        that.loading = true
        axios({
          url:'/archives/mainnetwork/update-load',
          method: 'get',
          params: {id: that.params.id}
        }).then(response=>{
          that.loading = false
          that.type=response.result.meterSort
          that.serachParam.stationId=response.result.substationId
          that.data = response.result
        })
      },
      onSubmit(){
        this.form.validateFields((err,values) => {
          if (!err) {            
            const that = this
            that.data = Object.assign(that.data,values)
            if(!that.params){
              return
            }
            that.data.status = that.data.status[0]
            that.data.volLevel = that.data.volLevel[0]
            that.data.meterSort = that.data.meterSort[0]
            that.data.measMode = that.data.measMode[0]
            that.data.important = that.data.important[0]
            if(that.data.meterSort=='4'){
              that.data.lineId=''
            }else{
              that.data.substationMtId=''
            }
            that.loading = true
            axios({
              url: that.params.opType=='1'?'/archives/mainnetwork/insert':'/archives/mainnetwork/update',
              method: 'post',
              params: that.data
            }).then(result=>{
              that.loading = false
              if(result){
                this.$emit('ok',result)
              }
            })
          }
        })
      },
      onChange(value){
         this.type=value
         this.serachParam.sort=value=='0'?'0':(value=='7'?'1':'')
      },
      onCancel(){
        this.$emit('cancel')
      },
    }
  }
</script>
<style lang="scss" scoped>
  .card{
    margin-bottom: 24px;
  }
</style>
